        submitParam.formList.push(this.ruleForm);
        submitParam.formList[0].acceptorAgencyId = this.acceptorAgencyId;
        submitParam.formList[0].acceptorAgencyName = this.acceptorAgencyName;
        submitParam.formList[0].formType = 3;

        return submitParam;

