          });
          return
        }
        this.$refs["ruleForm"].validate((valid) => {
          if (valid) {
            if (this.$route.query.ifold == 1 || (this.creatOrUpdate === true)) {
              this.$Modal.confirm({
                title: '',
                content: '<p></p>',
                onOk: () => {

                  this.updateContent('ruleForm');

                },
                onCancel: () => {
                  this.$Message.info('');
                }
              });

            } else {
              this.$Modal.confirm({
                title: '',
                content: '<p></p>',
                onOk: () => {

                  this.submitContent('ruleForm');

                },
                onCancel: () => {
                  this.$Message.info('');
                }
              });
            }

          } else {
            console.log('error submit!!');
            this.$Message.info('');
            return false;
          }
        });


      },
      //
      beSure() {
        this.active++;
      },
      initSize(value){
        console.log(value);

        let waitAccparams = {
          page: value,
          size: 10,

        }
        if (this.time[0] !== '') {
          waitAccparams.time = this.time;
        }
        if (this.applyState !== '') {
          waitAccparams.states = [this.applyState, this.applyState];

        }
        if (this.applyType !== '') {
          waitAccparams.applyTypeId = this.applyType;
        }
        this.getOrders(waitAccparams);

      },

      handleSuccess (res, file) {
        //

        // this.uploadList = this.$refs.upload.fileList;
        //this.uploadList[0].name="";
        if (this.uploadList[0].url === '') {
          this.uploadList[0].url = res.data.thumbnail;
          this.pdfList.push(res.data.preview)
        } else {
          this.uploadList.push({"url":res.data.thumbnail});
          this.pdfList.push(res.data.preview)

        }

      },
      handleRemove(res, file) {
        for (let i = 0; i < this.uploadList.length; i++) {
          if(res.response){
            if (this.uploadList[i].url ==res.response.data.thumbnail) {
              this.uploadList.splice(i, 1);
            }
          }
          if( res.url){
            if (this.uploadList[i].url.split("=")[1] == res.url.split("=")[1]) {

              this.uploadList.splice(i, 1);
            }
          }

        }
        if (this.uploadList.length == '') {
          this.uploadList = [
            {"url": ''}
          ];
        }
      },
      handleBeforeUpload () {

      },
      handleBeforeUpload1 () {
        //   this.file1NameNum++;
        this.fileList[0]++;
      },
      handleBeforeUpload2 () {
        //  this.file2NameNum++;
        this.fileList[1]++;
      },
      handleBeforeUpload3 () {
        // this.file3NameNum++;
        this.fileList[2]++;
      },
//      handleBeforeUpload4 () {
//        this.file4NameNum++;
//      },
//      handleBeforeUpload5 () {
//        this.file5NameNum++;
//      },
      handleFormatError (file) {
        this.$Notice.warning({
          title: '',
          desc: 'pdf'
        });
      },
      handleFormatError1(file) {
        this.$Notice.warning({
          title: '',
          desc: 'xlsx'
        });
      },
      handleView(index){
        console.log(index);
        this.visible = true;
        this.pdf = this.pdfList[index];
      },
      //
      save(type){
        const title = '';
        const content = '<p></p>';
        this.$Modal.success({
          title: title,
          content: content

        });
        this.$router.push('applyerList');

      },
      exactSearch(){
        if (this.cylinderEqCode) {
          let params = 'eqCode=' + this.cylinderEqCode+'';
          setAppService.getCylinder(params).then(res => {
              if (res.status === 200) {
                if(res.data){
                  this.editCylinderData=[];
                  this.editCylinderData.push(res.data);
                }else {
                  this.editCylinderData=[];
                  this.$Message.success('');
                }
                // this.num=res.data.totalElements;
              }else if(res.status=== 401){
                this.$Notice.error({
                  title: '',
                  desc: res.msg
                });
              }
            }
          ).catch(error => {
            console.log(error);
          })
        }
      },
      exactSearchPipe(){
        if (this.pipeEqCode) {
          let params = 'eqCode=' + this.pipeEqCode+'';
          setAppService.getPipe(params).then(res => {
              if (res.status === 200) {
                if(res.data){
                  this.editPipeData=[];
                  this.editPipeData.push(res.data);
                }else {
                  this.editPipeData=[];
                  this.$Message.success('');
                }
              }else if(res.status=== 401){
                this.$Notice.error({
                  title: '',
                  desc: res.msg
                });
              }
            }
          ).catch(error => {
            console.log(error);
          })
        }
      },
      saveCylinderInline (index, success, fail) {
        console.log(this.editCylinderData[index]);
        let params = {};
        params=this.editCylinderData[index];
        setAppService.changeCylinder(params).then(res => {
            if (res.status === 200) {
              success(() => {
                this.$Message.success('');
              });
            }else{
              fail(() => {
                this.$Message.error(',');
              });
            }
          }
        ).catch(error => {
          console.log(error);
        })

      },
      savePipeInline (index, success, fail) {
        console.log(this.editPipeData[index]);
        let params = {};
        params=this.editPipeData[index];
        setAppService.changePipe(params).then(res => {
            if (res.status === 200) {
              success(() => {
                this.$Message.success('');
              });
            }else{
              fail(() => {
                this.$Message.error(',');
              });
            }
          }
        ).catch(error => {
          console.log(error);
        })
      },

      //
      instance (type) {
        for(let i=0;i<this.fileList.length;i++){
          if(this.fileList[i]<=1){
            this.$Notice.warning({
              title: '',
              desc: 'pdf'
            });
            return
          }
        }
        this.$Modal.confirm({
          title: 'Title',
          content: '<p></p><p></p>',
          onOk: () => {
            let params = 'applyId=' + 1;
            setAppService.confrimApp(params).then(res => {
              if (res) {
                this.$router.push('applyerList');
              }
            }).catch(error => {
              console.log(error);
            })

          },
          onCancel: () => {
            this.$Message.info('');
          }
        });

      },

    },


  }
</script>
